import React, { useEffect, useState } from 'react';
import { Container } from 'reactstrap';

import banner from 'src/assets/images/CompanySingle/company-banner.png';

import Details from './CompanyDetails';
import axios from "axios";
const CompanyDetails = ({ match: { params } }) => {
  const token = localStorage.getItem('token');
  const { userID } = (params);
  const [user, setUser] = useState();

  var config2 = {
    headers: {
      'Authorization': token
    }
  }

  useEffect(() => {
    function fetchData() {
      axios.get(`https://www.togedoortestgo.site/graphql/accounts/${userID}`, config2).then((res) => {
        setUser(res.data);
      });
    }
    fetchData();
  }, [userID]);

  return (
    <>
      {user ? (
        <>
          <img src={banner} alt="company banner" className="img-fluid" />
          <Details user={user} />
        </>
      ) : <div>Loading...</div>}
    </>
  );
};

export default CompanyDetails;
