import React, { useState, useEffect } from "react";
import { Container, Row, Col } from 'reactstrap';
import axios from "axios";
import Thumbnail from 'src/components/Thumbnail';
import {
  faThLarge,
  faMapMarkerAlt,
} from '@fortawesome/free-solid-svg-icons';
import CompanyDescription from './CompanyDescription';
import CompanyOverview from './CompanyOverview';
import BestJobsCard from './BestJobsCard';
import DashboardCard from 'src/components/DashboardCard';

const CompanyDetails = (props) => {

  const [services, setServices] = useState([]);
  const user = props.user

  useEffect(() => {
    async function fetchData() {
      const response = await axios.get(`https://www.togedoortestgo.site/graphql/services`);
      setServices(response.data);
    }

    fetchData();
  }, []);

  const data = [
    {
      id: 1,
      thumbnailImg: props.user.picture,
      title: props.user.firstName + " " + props.user.lastName,
      jobCategory: props.user.jobTitle,
      jobCategoryIcon: faThLarge,
      location: props.user.location,
      locationIcon: faMapMarkerAlt,
    },
  ];

  return (

    <Container className="company-single">
      <Row>
        <Col lg={12} md={12}>
          {data && data.map((item) => <Thumbnail key={item.id} {...item} />)}
        </Col>
        <Col md={12} sm={12} lg={8}>
          <CompanyDescription user={props.user} />
          <DashboardCard title="My projects" className="mb-5" modalName="about">
            {services &&
              user &&
              services
                .filter((service) => service.userID === user.id)
                .map((service, i) => <BestJobsCard key={i} {...service} />)
            }
          </DashboardCard>

        </Col>
        <Col md={12} sm={12} lg={4}>
          <CompanyOverview user={props.user} />
        </Col>
      </Row>
    </Container>
  );
};

export default CompanyDetails;
